@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{display:flex;flex-direction:column;align-items:stretch;justify-content:space-between;height:100vh;color:white;text-align:center;background-color:#272727;font-family:'Lato', sans-serif}.App *{font-family:'Lato', sans-serif}.App .App_content{flex:1 1;display:flex;justify-content:space-between;align-items:stretch;overflow:hidden}.App .App_content>*{flex:1 1}.App .Loader{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}

.Loader{display:flex}.Loader .Loader_dot{display:block;width:0.8em;height:0.8em;margin-right:0.8em;background-color:black;border:3px solid white;-webkit-animation-name:bounce;animation-name:bounce;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}@-webkit-keyframes bounce{from{transform:translateY(0px) scale(0.7, 1.1)}30%{transform:translateY(-30px);-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}50%{transform:translateY(0px)}55%{transform:translateY(5px) scale(1.3, 0.7)}65%{transform:translateY(0px)}100%{transform:translateY(0px)}}@keyframes bounce{from{transform:translateY(0px) scale(0.7, 1.1)}30%{transform:translateY(-30px);-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}50%{transform:translateY(0px)}55%{transform:translateY(5px) scale(1.3, 0.7)}65%{transform:translateY(0px)}100%{transform:translateY(0px)}}

.Header{flex:0 0 auto;box-sizing:border-box;display:flex;justify-content:space-between;align-items:center;padding:0.2em 10vw;background-color:#169cb4;color:#ffdd55;width:100%;box-shadow:0px -20px 16px 20px rgba(0,0,0,0.25)}.Header h1{margin:0.4em 0.8em;font-size:1em;text-align:left}.Header .Header_logo{position:relative;display:flex;justify-content:center;align-items:center;margin:0.3em 0.1em;width:2.7em;height:2.7em}.Header .Header_logo span{position:absolute;display:block;width:100%;height:100%;background-position:center;background-size:contain;background-repeat:no-repeat;transform-origin:center;transform:scale(0.9);transition:transform 0.1s ease-out;-webkit-filter:hue-rotate(180deg) contrast(0) brightness(100);filter:hue-rotate(180deg) contrast(0) brightness(100)}.Header .Header_logo img{width:100%;z-index:10;transition:transform 0.1s ease-out, -webkit-filter 0.1s ease-out;transition:transform 0.1s ease-out, filter 0.1s ease-out;transition:transform 0.1s ease-out, filter 0.1s ease-out, -webkit-filter 0.1s ease-out}.Header .Header_logo:hover span{transform:scale(1.2) translate(-0.06em, -0.02em)}.Header .Header_logo:hover img{-webkit-filter:brightness(1.2);filter:brightness(1.2)}.Header .Header_title{margin-left:0.8em;height:1.3em}

.HeaderNavigation{display:block;height:auto}.HeaderNavigation a{margin-left:0.5em;padding:0.6em 1.3em;border:0.1em solid transparent;background-color:transparent;color:white;font-size:1.1em;font-weight:normal;text-decoration:none;transition:background-color 0.1s ease-out}.HeaderNavigation a:hover{background-color:rgba(255,255,255,0.05)}.HeaderNavigation a.active{border-color:white;color:white;background-color:rgba(0,42,121,0.2);font-weight:bold}.HeaderNavigation a.primary{color:#eb5626;background-color:#ffdd55;border:1px solid #ffdd55}.HeaderNavigation a.primary:hover{background-color:#ffe57b}

.PacksPage{display:flex;flex-direction:column;align-items:center;margin:50px 0}.PacksPage .PackListEntry{margin:10px 0}

.PackListEntry{display:block;width:400px;height:100px;padding:10px;background-color:#4e4e4e;color:#cfcfcf;box-shadow:5px 5px 6px 0px rgba(0,0,0,0.05)}.PackListEntry header h1{margin:0 0 10px 0;font-size:1.4em;text-align:left}.PackListEntry p{text-align:left;margin:0}

.HomePage .HomePage_banner{width:100%;-webkit-filter:hue-rotate(-170deg) brightness(0.6) contrast(3.5) brightness(1.7) saturate(0);filter:hue-rotate(-170deg) brightness(0.6) contrast(3.5) brightness(1.7) saturate(0)}

.PlayerCounter{margin:100px auto;position:relative;width:300px;padding:2em;color:#9a1616}.PlayerCounter::before{content:"";position:absolute;top:-4px;right:-4px;bottom:-4px;left:-4px;display:block;background-color:#eb5626;transform:rotate(2deg);z-index:-1}.PlayerCounter::after{content:"";position:absolute;top:-4px;right:-4px;bottom:-4px;left:-4px;display:block;background-color:#ffdd55;transform:rotate(-2deg);z-index:-1}.PlayerCounter p{margin:0;font-size:1.2em}.PlayerCounter p.count{margin:0.1em 0 0.7em 0;font-size:2em;font-weight:bold}

.Button{display:inline-block;margin-left:0.5em;padding:0.6em 1.3em;background-color:transparent;color:currentColor;border:1px solid currentColor;font-size:1.1em;font-weight:normal;text-decoration:none;transition:background-color 0.1s ease-out}.Button:hover{background-color:rgba(255,255,255,0.05)}.Button.active{border-color:white;color:white;background-color:rgba(0,42,121,0.2);font-weight:bold}.Button.primary{color:#eb5626;background-color:#ffdd55;border:1px solid #ffdd55}.Button.primary:hover{background-color:#ffe57b}

